import React from 'react';
import react from '@servicenow/ui-renderer-react';
import PropTypes from 'prop-types';
import createModalComponent from '../utils/modalUtils';
import SnMessageContent from './subcomponents/SnMessageContent';

const Modal = createModalComponent(SnMessageContent);

class SnMessageModal extends React.Component {
	constructor(props) {
		super(props);
	}

	renderText(message) {
		const subTexts = message.split('\n');
		return subTexts.map((text, index) => (
			<React.Fragment key={index}>
				<span>{text}</span>{' '}
				{index < subTexts.length - 1 ? <br /> : null}{' '}
			</React.Fragment>
		));
	}

	render() {
		const { message, ...moreProps } = this.props;
		const props = {
			...moreProps,
			shouldFocusOnOpen: true
		};
		return <Modal {...props}>{this.renderText(message)}</Modal>;
	}
}

SnMessageModal.defaultProps = {
	...Modal.defaultProps
};

SnMessageModal.propTypes = {
	...Modal.propTypes,
	message: PropTypes.string.isRequired
};

export default SnMessageModal;
