import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import { default as SnModal, NOW_MODAL_BUTTON_CLICKED } from './SnModal';
import style from './SnModal.scss';
import {
	SN_SCRIPTED_MODAL_DATA_SET,
	SN_SCRIPTED_MODAL_BUTTON_ENABLED_SET,
	ModalButtonStyle,
	SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN
} from '../../constants';
import { createActionPayloadToStateHandler } from '@devsnc/library-form';

const NOW_MODAL_OPENED_SET = 'NOW_MODAL#OPENED_SET';
const MODAL_DATA_PROP = 'modalData';

const buttonClickReducer = {
	effect: ({ updateState, action, dispatch }) => {
		updateState({
			buttonClicked: action,
			timestamp: Date.now()
		});
		dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, false);
	},
	stopPropagation: true
};
export default createCustomElement('sn-scripted-modal', {
	renderer: {
		type: react,
		view: SnModal
	},

	properties: {
		show: {},
		size: {},
		modalTitle: {},
		headerHidden: {},
		buttons: {},
		onButtonClick: {},
		buttonAlignment: {},
		actionMetaData: {},
		context: {},
		noSpacing: {},
		shouldFocusOnOpen: {}
	},

	actionHandlers: {
		[NOW_MODAL_BUTTON_CLICKED]: buttonClickReducer,
		[NOW_MODAL_OPENED_SET]: buttonClickReducer,
		[SN_SCRIPTED_MODAL_DATA_SET]: {
			effect: createActionPayloadToStateHandler(MODAL_DATA_PROP),
			stopPropagation: true
		},
		[SN_SCRIPTED_MODAL_BUTTON_ENABLED_SET]: ({
			state: {
				properties: { buttons }
			},
			updateProperties,
			action: { payload }
		}) => {
			const primaryButtonIndex = buttons.findIndex(
				button => button.buttonStyle === ModalButtonStyle.CONFIRM
			);
			if (primaryButtonIndex > -1) {
				const newButtons = [...buttons];
				newButtons.splice(primaryButtonIndex, 1, {
					...buttons[primaryButtonIndex],
					disabled: !payload
				});
				updateProperties({ buttons: newButtons });
			}
		}
	},
	styles: style
});

/**
 *
 * Listens to
 * "[MODAL-COMPONENT]/BUTTON_CLICKED";
 *
 *
 * BUTTON_CLICKED payload
 *
 * {
 *   buttonClicked: {
 		modalId: [String]
		title: [String],
		buttonStyle:['desctructive' | 'default'],
		buttonType:['header-button' | 'footer-button']
	 }
   }
 */
