import React from 'react';
import { t } from 'sn-translate';
import '../components/FieldSetModalElement';
import { ModalButtonStyle, BUTTON_TYPE_ARRAY } from '../../modal/constants';
import { toAttributes } from '../utils/utils';

const ComponentId = 'sn-form-field-wrapper-modal';
const DEFAULT_CANCEL_TITLE = t('Cancel');
const DEFAULT_CONFIRM_TITLE = t('OK');
const DEFAULT_BUTTONS = [
	{ title: DEFAULT_CONFIRM_TITLE, buttonStyle: ModalButtonStyle.CONFIRM },
	{ title: DEFAULT_CANCEL_TITLE }
];
import { ATFHelper } from '@devsnc/library-form';

class FieldSetWrapperModal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			show: props.show,
			modalTitle: '',
			fields: [],
			buttons: DEFAULT_BUTTONS
		};
		this._onButtonClick = this._onButtonClick.bind(this);
		this._promiseResolve = null;
		this._promiseReject = null;
		this.atfHelper = ATFHelper(window);
	}

	componentWillReceiveProps(changes) {
		if (changes.show != this.state.show) {
			this.setState({ show: changes.show });
		}
	}

	_onButtonClick(button, data) {
		this.setState({ show: false });

		switch (button.buttonStyle) {
			case ModalButtonStyle.CONFIRM:
			case ModalButtonStyle.DESTRUCTIVE:
				if (this._promiseResolve) {
					this._promiseResolve(data);
				}
				break;
			default:
				if (this._promiseReject) {
					this._promiseReject();
				}
		}
		this._promiseResolve = null;
		this._promiseReject = null;
	}

	open({
		title,
		fields,
		size,
		confirmTitle,
		confirmType,
		cancelTitle,
		cancelType,
		instruction
	}) {
		if (BUTTON_TYPE_ARRAY.indexOf(cancelType) === -1) {
			cancelType = ModalButtonStyle.DEFAULT;
			console.error(
				'Incorrect cancelType applied, please use one of the following options: ' +
					BUTTON_TYPE_ARRAY
			);
		}
		if (BUTTON_TYPE_ARRAY.indexOf(confirmType) === -1) {
			confirmType = ModalButtonStyle.CONFIRM;
			console.error(
				'Incorrect confirmType applied, please use one of the following options: ' +
					BUTTON_TYPE_ARRAY
			);
		}
		this.setState({
			modalTitle: title,
			show: true,
			fields: fields || [],
			size,
			instruction,
			buttons: [
				{
					title: confirmTitle || DEFAULT_CONFIRM_TITLE,
					buttonStyle: confirmType || ModalButtonStyle.CONFIRM
				},
				{
					title: cancelTitle || DEFAULT_CANCEL_TITLE,
					buttonStyle: cancelType || ModalButtonStyle.CANCEL
				}
			]
		});

		this.atfHelper.initAtfModalButtonInterceptor();
		return new Promise((resolve, reject) => {
			this._promiseResolve = resolve;
			this._promiseReject = reject;
		});
	}

	render() {
		if (!this.state.show) {
			return null;
		}

		const attrs = toAttributes(this.state);
		return (
			<sn-scripted-modal-field-set
				should-focus-on-open={true}
				{...attrs}
				on-button-click={this._onButtonClick}
			/>
		);
	}
}

export default FieldSetWrapperModal;
