import React from 'react';
import { render } from 'react-dom';
import SnAlertModal from './SnAlertModal';
import SnIFrameWrapperModal from './SnIFrameWrapperModal';
import _ from 'lodash';
import { version, name } from '../../../../package.json';

const GLOBAL_INSTANCE_KEY = `${name}-${_.head((version || '').split('.')) ||
	''}-appinstances`;
const MODAL_CONTAINER = 'modalContainer';
const REGISTERED_MODAL_CONTAINER = 'registeredModalContainer';
const INITIALIZE_PROMISE = 'initializePromise';
const RESOLVE_INITIAL_MODALS = 'resolveInitialPromise';
const REGISTERED_MODALS = 'registeredModals';
const HAS_REGISTERED_DEFAULT_MODALS = 'hasRegisteredDefaultModals';
const SN_APP_MODAL = 'snAppModal';

const globalInstanceKey = Symbol.for(GLOBAL_INSTANCE_KEY);
if (!window[globalInstanceKey]) {
	let _resolvePromise;
	const _initialPromise = new Promise(resolve => {
		_resolvePromise = resolve;
	});
	window[globalInstanceKey] = {
		[MODAL_CONTAINER]: null,
		[REGISTERED_MODAL_CONTAINER]: null,
		[REGISTERED_MODALS]: [],
		[INITIALIZE_PROMISE]: _initialPromise,
		[RESOLVE_INITIAL_MODALS]: _resolvePromise,
		[HAS_REGISTERED_DEFAULT_MODALS]: false,
		[SN_APP_MODAL]: {}
	};
}

const getInstance = type => {
	return window[globalInstanceKey][type];
};

const setInstance = (type, value) => {
	window[globalInstanceKey][type] = value;
};

let SnAppModal = getInstance(SN_APP_MODAL);

export const getModalComponentKey = (type, namespace) => {
	return namespace ? `__CUSTOM_MODAL_COMPONENT` : `${type}_MODAL_COMPONENT`;
};

const getRegisteredModals = () => {
	return getInstance(REGISTERED_MODALS) || [];
};

const renderRegisteredModals = () => {
	const modalContainer = getInstance(REGISTERED_MODAL_CONTAINER);
	if (!modalContainer) {
		// eslint-disable-next-line no-console
		console.warn(
			'SnAppModal failed to initialize. Please make sure to attach <SnAppModalContainer /> at the root node.'
		);
		return;
	}

	const renderComponents = new Set();
	const modals = getRegisteredModals()
		.map(modal => {
			const { component, type, namespace } = modal;
			if (renderComponents.has(component)) return null;
			renderComponents.add(component);
			const instanceKey = getModalComponentKey(type, namespace);
			return React.createElement(
				component,
				{
					key: type,
					ref: comp => {
						if (comp) {
							setInstance(instanceKey, comp);
							modal.isRendered = true;
						}
					}
				},
				null
			);
		})
		.filter(modal => modal !== null);

	render(<div>{modals}</div>, modalContainer);
};

const renderComponents = () => {
	const modalContainer = getInstance(MODAL_CONTAINER);
	let _resolvePromise = getInstance(RESOLVE_INITIAL_MODALS);

	//@see https://reactjs.org/docs/react-dom.html
	render(
		<div
			ref={node => {
				setInstance(REGISTERED_MODAL_CONTAINER, node);
				renderRegisteredModals();
				_resolvePromise();
			}}
		/>,
		modalContainer
	);
};

export const initialize = container => {
	const modalContainer = getInstance(MODAL_CONTAINER);

	if (!container) {
		// eslint-disable-next-line no-console
		console.warn('Container is required.');
		return;
	}

	if (modalContainer === container) {
		return;
	}

	if (modalContainer) return;
	setInstance(MODAL_CONTAINER, container);
	renderComponents();
};

const onInitialized = () => {
	const initializePromise = getInstance(INITIALIZE_PROMISE);
	return Promise.resolve(initializePromise);
};

const registerModal = (component, type, fnMapper, namespace) => {
	const registeredModals = getRegisteredModals();

	if (!component && typeof component === 'function') {
		// eslint-disable-next-line no-console
		console.warn('A modal react component is required.');
		return;
	}

	if (
		!fnMapper ||
		typeof fnMapper !== 'object' ||
		Object.keys(fnMapper).length == 0
	) {
		// eslint-disable-next-line no-console
		console.warn('Function mapper object is required.');
		return;
	}

	if (
		_.findIndex(
			registeredModals,
			reg => reg.type === type && reg.namespace === namespace
		) >= 0
	) {
		return;
	}

	const existFn = Object.values(fnMapper).filter(fn =>
		namespace
			? _.get(SnAppModal.customModals, `${namespace}.${fn}`)
			: _.get(SnAppModal, fn)
	);
	if (existFn.length > 0) {
		// eslint-disable-next-line no-console
		console.warn(
			`Public function name${
				existFn.length > 1 ? 's' : ''
			} '${existFn}' ${
				existFn.length > 1 ? 'are' : 'is'
			} already existed.`
		);
		return;
	}

	setInstance(REGISTERED_MODALS, [
		...registeredModals,
		{
			component,
			type,
			fnMapper,
			namespace
		}
	]);

	let modalObj = namespace ? SnAppModal.customModals[namespace] : SnAppModal;
	if (!modalObj) modalObj = {};
	_.forEach(fnMapper, (publicFnName, componentFnName) => {
		modalObj[publicFnName] = (...args) => {
			const instanceKey = getModalComponentKey(type, namespace);
			const compInstance = getInstance(instanceKey);
			if (!compInstance) {
				const msg = `SnAppModal.${publicFnName} is not ready. ${type} is not rendered yet.`;
				// eslint-disable-next-line no-console
				console.warn(msg);
				return Promise.reject(msg);
			}

			return namespace
				? compInstance[componentFnName].apply(compInstance, [
						type,
						...args
				  ])
				: compInstance[componentFnName].apply(compInstance, args);
		};
	});

	onInitialized().then(() => {
		renderRegisteredModals();
	});
};

const registerDefaultModals = () => {
	const isRegisteredDefaultModals = getInstance(
		HAS_REGISTERED_DEFAULT_MODALS
	);
	if (!isRegisteredDefaultModals) {
		setInstance(HAS_REGISTERED_DEFAULT_MODALS, true);
		registerModal(SnAlertModal, 'SnAlertModal', {
			info: 'info',
			confirm: 'confirm',
			confirmDestroy: 'confirmDestroy'
		});
		registerModal(SnIFrameWrapperModal, 'SnIFrameWrapperModal', {
			load: 'showFrame'
		});
	}
};

SnAppModal.registerModal = registerModal;
SnAppModal.onInitialized = onInitialized;
SnAppModal.initialize = initialize;
SnAppModal.getRegisteredModals = getRegisteredModals;
SnAppModal.customModals = {};
registerDefaultModals();

export default SnAppModal;
