import React from 'react';
import react from '@servicenow/ui-renderer-react';
import '../modal/SnModalElement';
import SnModal from '../modal/SnModal';
import PropTypes from 'prop-types';
import { ModalSize } from '../../constants';

export const ModalDefaultProps = {
	...SnModal.defaultProps
};

export const ModalPropTypes = {
	...SnModal.propTypes,
	modalId: PropTypes.string
};

export default function createModalComponent(
	ContentComponent,
	contentProps = {}
) {
	class Modal extends React.Component {
		constructor(props) {
			super(props);
		}

		render() {
			const {
				modalId,
				modalTitle,
				headerHidden,
				buttonAlignment,
				children,
				...moreProps
			} = this.props;
			const compId = modalId ? { 'component-id': modalId } : {};
			const { context = '' } = moreProps;
			const className = context
				.trim()
				.split(' ')
				.reduce((acc, context) => {
					return `${acc} context-${context}`;
				}, '')
				.trim();

			return (
				<sn-scripted-modal
					{...compId}
					modal-title={modalTitle}
					header-hidden={headerHidden}
					button-alignment={buttonAlignment}
					className={className}
					{...moreProps}
				>
					<ContentComponent {...contentProps}>
						{children}
					</ContentComponent>
				</sn-scripted-modal>
			);
		}
	}

	Modal.defaultProps = ModalDefaultProps;
	Modal.propTypes = ModalPropTypes;

	return Modal;
}
