import React from 'react';
import react from '@servicenow/ui-renderer-react';
import PropTypes from 'prop-types';
import SnEmbeddedContent from '../../embededModal/subcomponents/SnEmbeddedContent';
import {
	IFRAME_MODAL_MESSAGE_TYPE,
	IFRAME_MODAL_ACTION_INIT,
	IFRAME_MODAL_ACTION_CONFIRMED,
	IFRAME_MODAL_ACTION_CANCELED,
	IFrameAutoCloseOn
} from '../constants';

export default class SnIframeContent extends React.Component {
	constructor(props) {
		super(props);
		this.bindRef = this.bindRef.bind(this);
		this.iFrame = null;
		this.postIFrameMessage = this.postIFrameMessage.bind(this);
		this.iFrameOnLoad = this.iFrameOnLoad.bind(this);
		this.initialized = false;
		this.iFramePathName = '';
	}

	bindRef(ref) {
		this.iFrame = ref;
	}

	iFrameOnLoad() {
		if (!this.props && !this.iFrame) {
			return;
		}

		const { autoCloseOn } = this.props;
		const iframeWindow = this.iFrame.contentWindow;

		if (!this.initialized) {
			this.postIFrameMessage();
			this.iFramePathName = iframeWindow.location.pathname;
			this.initialized = true;
		} else {
			if (!autoCloseOn || Object.keys(autoCloseOn).length == 0) {
				return;
			}
			let isSuccess = false;
			switch (autoCloseOn) {
				case IFrameAutoCloseOn.EMPTY_BODY: {
					const content = iframeWindow.document.body;
					isSuccess = !content || !content.innerHTML;
					break;
				}
				case IFrameAutoCloseOn.URL_CHANGED: {
					isSuccess =
						iframeWindow.location.pathname !== this.iFramePathName;
					break;
				}
			}

			isSuccess ? this.postConfirm() : this.postCancel();
		}
	}

	payload(action, additions = {}) {
		const { modalId } = this.props;
		return {
			messageType: IFRAME_MODAL_MESSAGE_TYPE,
			modalAction: action,
			modalId,
			...additions
		};
	}

	postConfirm() {
		const payload = this.payload(IFRAME_MODAL_ACTION_CONFIRMED);
		window.postMessage(payload, location.origin);
	}

	postCancel() {
		const payload = this.payload(IFRAME_MODAL_ACTION_CANCELED);
		window.postMessage(payload, location.origin);
	}

	postIFrameMessage() {
		if (!this.props && !this.iFrame) {
			return;
		}

		const { src } = this.props;
		const payload = this.payload(IFRAME_MODAL_ACTION_INIT, { src });
		this.iFrame.contentWindow.postMessage(payload, location.origin);
	}

	componentDidMount() {
		if (this.iFrame) {
			this.iFrame.addEventListener('load', this.iFrameOnLoad);
		}
	}

	render() {
		const { src, size, height, modalTitle } = this.props;
		const attributes = height ? { height } : {};

		//FIXME: Remove EmbededContent, once Seismic supports children props.
		return (
			<SnEmbeddedContent {...attributes}>
				<iframe
					src={src}
					ref={this.bindRef}
					title={modalTitle}
					sandbox="allow-same-origin allow-forms allow-scripts allow-popups-to-escape-sandbox allow-popups allow-modals"
					className={`sn-modal-iframe--${size}`}
				/>
			</SnEmbeddedContent>
		);
	}
}

SnIframeContent.propTypes = {
	src: PropTypes.string.isRequired,
	modalId: PropTypes.string.isRequired,
	height: PropTypes.string,
	modalTitle: PropTypes.string,
	autoCloseOn: PropTypes.oneOf(Object.values(IFrameAutoCloseOn))
};
