import React from 'react';
import react from '@servicenow/ui-renderer-react';
import PropTypes from 'prop-types';
import {
	default as createModalComponent,
	ModalDefaultProps,
	ModalPropTypes
} from '../utils/modalUtils';
import SnIFrameContent from './subcomponents/SnIFrameContent';
import { IFRAME_CLOSED } from '../../constants';
import {
	IFRAME_MODAL_MESSAGE_TYPE,
	IFRAME_MODAL_ACTION_CONFIRMED,
	IFRAME_MODAL_ACTION_CANCELED,
	IFrameAutoCloseOn
} from './constants';

class SnIFrameModal extends React.Component {
	constructor(props) {
		super(props);
		this.messageHandler = this.messageHandler.bind(this);
		this.onButtonClick = this.onButtonClick.bind(this);
	}

	componentDidMount() {
		window.addEventListener('message', this.messageHandler);
	}

	componentWillUnmount() {
		window.removeEventListener('message', this.messageHandler);
	}

	messageHandler(e) {
		const {
			componentId,
			properties: { show }
		} = this.props;
		if (!show) {
			return;
		}

		const { messageType, modalAction, data, modalId } = e.data;
		if (
			messageType === IFRAME_MODAL_MESSAGE_TYPE &&
			modalId === componentId &&
			[
				IFRAME_MODAL_ACTION_CONFIRMED,
				IFRAME_MODAL_ACTION_CANCELED
			].includes(modalAction)
		) {
			this.dispatchEvent(modalAction, data);
		}
	}

	onButtonClick() {
		this.dispatchEvent(IFRAME_MODAL_ACTION_CANCELED);
	}

	dispatchEvent(action, result) {
		const { onClose } = this.props.properties;

		const { dispatch } = this.props;
		const payload = {
			modalId: this.props.componentId,
			modalAction: action,
			data: result
		};
		dispatch(IFRAME_CLOSED, payload);

		if (onClose) {
			onClose(payload);
		}
	}

	render() {
		const {
			src,
			show,
			height,
			modalTitle,
			headerHidden,
			size,
			autoCloseOn,
			context = '',
			shouldFocusOnOpen
		} = this.props.properties;
		const { componentId } = this.props;

		const moreProps = {
			show,
			modalTitle,
			headerHidden,
			size,
			context: `${context} iframe`,
			shouldFocusOnOpen
		};

		const Modal = createModalComponent(SnIFrameContent, {
			src,
			modalId: componentId,
			autoCloseOn,
			size,
			height,
			modalTitle
		});
		if (!show) {
			return null;
		}
		return <Modal {...moreProps} onButtonClick={this.onButtonClick} />;
	}
}

SnIFrameModal.defaultProps = {
	...ModalDefaultProps
};

SnIFrameModal.propTypes = {
	...ModalPropTypes,
	src: PropTypes.string.isRequired,
	onClose: PropTypes.func,
	autoCloseOn: PropTypes.oneOf(Object.values(IFrameAutoCloseOn)),
	height: PropTypes.string
};

export default SnIFrameModal;
