import React from 'react';

const SLOT_STYLES = { height: '100%' };

//FIXME: This slot is work around since Seismic doesn't pass children props
const ModalContent = props => {
	const { children, slot, height } = props;
	const style = height ? { height } : SLOT_STYLES;
	return (
		<div slot={slot} style={style} className="slot-content">
			{children}
		</div>
	);
};

export default ModalContent;
