import React from 'react';
import ModalContent from '../../modal/subcomponents/SnModalContent';

export default function SnEmbeddedContent(props) {
	const { children, height } = props;
	const attributes = height ? { height } : {};
	return (
		<ModalContent slot="content" {...attributes}>
			{children}
		</ModalContent>
	);
}
