import React from 'react';
import PropTypes from 'prop-types';
import {
	controlElementFactory,
	FieldType
} from '@devsnc/sn-glide-form-controls';

export const BasePropTypes = {
	fields: PropTypes.arrayOf(
		PropTypes.shape({
			name: PropTypes.string.isRequired,
			label: PropTypes.string.isRequired,
			mandatory: PropTypes.bool.isRequired,
			type: PropTypes.string.isRequired,
			value: PropTypes.string,
			// choices
			choices: PropTypes.arrayOf(
				PropTypes.shape({
					value: PropTypes.string,
					displayValue: PropTypes.string
				})
			),
			autofocus: PropTypes.bool,

			//reference field
			recordsysid: PropTypes.string,
			tablename: PropTypes.string,
			referencetable: PropTypes.string
		})
	),
	instruction: PropTypes.string,
	shouldFocusOnOpen: PropTypes.bool
};

const fieldConverter = (field, autofocus) => {
	const autofocusProp = autofocus ? { autofocus: true } : {};
	switch (field.type) {
		case FieldType.REFERENCE:
			return {
				...field,
				fieldname: field.name,
				...autofocusProp,

				// modal field sets are rendered outside form so we must specificy to grab DA via a network request, false by default
				// otherwise unnecessary network call are made when controls are rendered inside the form since there is no way to identify
				// whether the control is loaded outside a form or not so this flag will exist to make that decision
				fetchDeclarativeActions: true
			};
		default:
			return {
				...field,
				...autofocusProp,
				fetchDeclarativeActions: true
			};
	}
};

class FieldSet extends React.PureComponent {
	constructor(props) {
		super(props);

		this.state = {
			fields: this.getSupportFields(props.fields)
		};
	}

	componentWillReceiveProps(props) {
		if (props.fields !== this.props.fields) {
			this.setState({ fields: this.getSupportFields(props.fields) });
		}
	}

	getSupportFields(fields = []) {
		let unsupportTypes = new Set();
		const supportedFields = fields.filter(field => {
			const support = this.isSupportedType(field.type);
			if (!support) {
				unsupportTypes.add(field.type);
			}
			return support;
		});

		if (unsupportTypes.size > 0) {
			const unsupportArr = Array.from(unsupportTypes);
			// eslint-disable-next-line no-console
			console.warn(
				`Type ${unsupportArr} ${
					unsupportArr.length > 1 ? 'are' : 'is'
				} not supported.`
			);
		}
		return supportedFields;
	}

	isSupportedType(type) {
		if (!controlElementFactory) {
			console.warn(
				`@devsnc/sn-glide-form-controls is required for modal-field-set`
			);
			return false;
		}

		if (controlElementFactory.isSupported(type)) {
			switch (type) {
				case FieldType.STRING:
				case FieldType.TEXTAREA:
				case FieldType.CHOICE:
				case FieldType.GLIDE_LIST:
				case FieldType.REFERENCE:
				case FieldType.HTML:
				case FieldType.BOOLEAN:
					return true;
			}
		}
		return false;
	}

	render() {
		const { fields = [] } = this.state;
		const {
			componentId,
			onValueChange,
			onStagedValueChange,
			instruction,
			shouldFocusOnOpen
		} = this.props;
		let hasFocused = false;
		if (!controlElementFactory) {
			console.warn(
				`@devsnc/sn-glide-form-controls is required for modal-field-set`
			);
			return null;
		}

		return (
			<React.Fragment>
				{instruction && (
					<span
						className="instruction"
						id="modal-field-set-instruction-span"
					>
						{instruction}
					</span>
				)}
				<div className="sn-form">
					{fields.map(f => {
						const autofocus =
							shouldFocusOnOpen && !hasFocused && !f.readOnly;
						hasFocused = hasFocused || autofocus;
						const { type } = f;
						const field = fieldConverter(f, autofocus);
						const Control = controlElementFactory.createFormControl(
							componentId,
							type,
							field,
							onValueChange,
							onStagedValueChange
						);
						return Control;
					})}
				</div>
			</React.Fragment>
		);
	}
}

FieldSet.proptypes = {
	...BasePropTypes,
	onValueChange: PropTypes.func,
	onStagedValueChange: PropTypes.func
};

export default FieldSet;
