import React from 'react';
import react from '@servicenow/ui-renderer-react';
import SnEmbeddedContent from '../embededModal/subcomponents/SnEmbeddedContent';
import { ModalButtonAlignment, ModalButtonStyle } from '../../constants';
import createModalComponent from '../utils/modalUtils';
import { t } from 'sn-translate';
import _ from 'lodash';
import { ATFHelper } from '@devsnc/library-form';

const ALERT_MODAL_ID = 'sn-modal-custom';
const DEFAULT_CONFIRM_TITLE = t('OK');
const DEFAULT_TITLE = t('Close');

class SnCustomModal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			show: false,
			modalTitle: '',
			component: ''
		};
		this.onButtonClick = this.onButtonClick.bind(this);
		this._promiseResolve = null;
		this._promiseReject = null;
		this.atfHelper = ATFHelper(window);
	}

	onButtonClick(button) {
		setTimeout(() => {
			this.setState({ show: false });
		});

		switch (button.buttonStyle) {
			case ModalButtonStyle.DEFAULT:
			case ModalButtonStyle.CONFIRM:
			case ModalButtonStyle.DESTRUCTIVE:
				if (this._promiseResolve) {
					this._promiseResolve(button);
				}
				break;
			default:
				if (this._promiseReject) {
					this._promiseReject(button);
				}
		}
		this._promiseResolve = null;
		this._promiseReject = null;
	}

	open(component, options) {
		const {
			title,
			cancelTitle,
			confirmTitle,
			size,
			params,
			disableConfirmOnLoad = false
		} = options;

		const buttons = [
			{
				title:
					confirmTitle ||
					(cancelTitle ? DEFAULT_CONFIRM_TITLE : DEFAULT_TITLE),
				buttonStyle: ModalButtonStyle.CONFIRM,
				disabled: disableConfirmOnLoad
			},
			...(cancelTitle ? [{ title: cancelTitle }] : [])
		];
		this.setState({
			show: true,
			modalTitle: title,
			buttons,
			component,
			params,
			size
		});

		this.atfHelper.initAtfModalButtonInterceptor();
		return new Promise((resolve, reject) => {
			this._promiseResolve = resolve;
			this._promiseReject = reject;
		});
	}

	render() {
		const { show, component, params = {} } = this.state;
		const { modalTitle, ...props } = this.state;
		const { modalId = ALERT_MODAL_ID, ...passingProps } = _.omit(
			this.props,
			['children']
		);
		if (!component || !show) return null;

		const Component = component;
		const Modal = createModalComponent(SnEmbeddedContent, params);
		return (
			<Modal
				modalId={modalId}
				modalTitle={modalTitle}
				{...props}
				{...passingProps}
				show={show}
				onButtonClick={this.onButtonClick}
				buttonAlignment={ModalButtonAlignment.FULL}
			>
				<Component {...params} />
			</Modal>
		);
	}
}

export default SnCustomModal;
