import React from 'react';
import react from '@servicenow/ui-renderer-react';
import { t } from 'sn-translate';
import { IFRAME_MODAL_TAG_NAME } from '../iframeModal/SnIFrameModalElement';
import { ModalSize } from '../../constants';
import { IFRAME_MODAL_ACTION_CONFIRMED } from '../iframeModal/constants';
import _ from 'lodash';

const IFRAME_MODAL_ID = 'sn-modal-iframe-singleton';

class SnIFrameWrapperModal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			show: false,
			modalTitle: '',
			src: '',
			size: ModalSize.XL
		};
		this.onClose = this.onClose.bind(this);
		this._promiseResolve = null;
		this._promiseReject = null;
	}

	onClose(result) {
		this.setState({ show: false });

		switch (result.modalAction) {
			case IFRAME_MODAL_ACTION_CONFIRMED:
				if (this._promiseResolve) {
					this._promiseResolve(result.data);
				}
				break;
			default:
				if (this._promiseReject) {
					this._promiseReject(result.data);
				}
				break;
		}
		this._promiseResolve = null;
		this._promiseReject = null;
	}

	load({ title, url, size = ModalSize.XL, height, autoCloseOn }) {
		if (!title) {
			const msg = t('Title is required');
			// eslint-disable-next-line no-console
			console.warn(msg);
			return Promise.reject(msg);
		}

		if (!url) {
			const msg = t('URL is required');
			// eslint-disable-next-line no-console
			console.warn(msg);
			return Promise.reject(msg);
		}

		this.setState({
			show: true,
			src: url,
			size,
			modalTitle: title,
			height,
			autoCloseOn
		});

		return new Promise((resolve, reject) => {
			this._promiseResolve = resolve;
			this._promiseReject = reject;
		});
	}

	render() {
		if (!this.state.show) {
			return null;
		}

		const { modalTitle, autoCloseOn, ...props } = this.state;
		const { modalId = IFRAME_MODAL_ID, ...passingProps } = _.omit(
			this.props,
			['children']
		);
		return (
			<IFRAME_MODAL_TAG_NAME
				component-id={modalId}
				modal-title={modalTitle}
				auto-close-on={autoCloseOn}
				should-focus-on-open={true}
				{...props}
				{...passingProps}
				on-close={this.onClose}
			/>
		);
	}
}

export default SnIFrameWrapperModal;
