import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import { default as FieldSet } from './FieldSet';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const handleUpdateState = ({ state: { properties }, updateState }) => {
	updateState({
		fields: properties.fields,
		onValueChange: properties.onValueChange
	});
};

export default createCustomElement('sn-form-internal-field-set', {
	renderer: {
		type: react,
		view: FieldSet
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: {
			effect: handleUpdateState
		},
		[COMPONENT_PROPERTY_CHANGED]: {
			effect: context => {
				const {
					action: {
						payload: { name, value, oldValue }
					}
				} = context;
				if (
					value !== oldValue &&
					('fields' === name || 'onValueChange' === name)
				)
					handleUpdateState(context);
			}
		}
	},
	properties: {
		fields: {
			default: []
		},
		instruction: {},
		shouldFocusOnOpen: {},
		onValueChange: {
			default: () => {}
		},
		onStagedValueChange: {
			default: () => {}
		}
	}
});
