import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import { default as FieldSetModal } from './FieldSetModal';
import {
	FORM_FIELD_MODAL_CONFIRMED,
	FORM_FIELD_MODAL_CANCELED
} from '../constants';
import style from './FieldSetModal.scss';

export default createCustomElement('sn-scripted-modal-field-set', {
	renderer: {
		type: react,
		view: FieldSetModal
	},

	actionTypes: [FORM_FIELD_MODAL_CONFIRMED, FORM_FIELD_MODAL_CANCELED],

	properties: {
		show: {},
		size: {},
		modalTitle: {},
		headerHidden: {},
		buttons: {},
		onButtonClick: {},
		buttonAlignment: {},
		height: {},
		fields: {},
		instruction: {},
		shouldFocusOnOpen: {}
	},
	styles: style
});

/**
 *
 * Listens to
 * "[FORM-FIELD-MODAL-COMPONENT]/FORM_FIELD_MODAL_CONFIRMED,
 *  [FORM-FIELD-MODAL-COMPONENT]/FORM_FIELD_MODAL_CANCELED";
 *
 *
 * FORM_FIELD_MODAL_CONFIRMED payload
 *
 * {
 		clickedButton: Button,
		updatedFields: [Field]
   }

 * FORM_FIELD_MODAL_CANCELED payload
 *
 * {
 		clickedButton: Button
   }

 */
