import {
	IFRAME_MODAL_MESSAGE_TYPE,
	IFRAME_MODAL_ACTION_CONFIRMED,
	IFRAME_MODAL_ACTION_CANCELED,
	IFRAME_MODAL_ACTION_INIT
} from '../../index';

function createPayload(action, modalId, data) {
	return Object.assign(
		{
			messageType: IFRAME_MODAL_MESSAGE_TYPE,
			modalAction: action,
			modalId: modalId
		},
		data ? { data: data } : {}
	);
}

function postMessage(window, payload) {
	if (window.parent === window) {
		// eslint-disable-next-line no-console
		console.warn(`Parent is missing. Is this called inside an iFrame?`);
		return;
	}
	window.parent.postMessage(payload, location.origin);
}

export default class IFrameMessagingHelper {
	constructor(window) {
		this.window = window;
		this.src = location.href;
		this.messageHandler = this.messageHandler.bind(this);
		this.window.addEventListener('message', this.messageHandler);
	}

	removeEvent() {
		this.window.removeEventListener('message', this.messageHandler);
	}

	messageHandler(e) {
		const { messageType, modalAction, modalId, src } = e.data;
		if (
			messageType !== IFRAME_MODAL_MESSAGE_TYPE ||
			modalAction !== IFRAME_MODAL_ACTION_INIT ||
			!this.src.match(src)
		) {
			return;
		}
		this.modalId = modalId;
	}

	confirm(data) {
		const payload = createPayload(
			IFRAME_MODAL_ACTION_CONFIRMED,
			this.modalId,
			data
		);
		postMessage(this.window, payload);
	}

	cancel(data) {
		const payload = createPayload(
			IFRAME_MODAL_ACTION_CANCELED,
			this.modalId,
			data
		);
		postMessage(this.window, payload);
	}
}
