import React from 'react';
import react from '@servicenow/ui-renderer-react';
import { render } from 'react-dom';
import _ from 'lodash';
import {
	default as SnAppModal,
	getModalComponentKey
} from '../appModal/SnAppModal';
import SnCustomModal from './SnCustomModal';

const MAX_MODAL_QUEUE_SIZE = 10;

export default class FormModal {
	constructor() {
		this._modalComponents = {};
		this.customModals = {};
		this.registeredCustomModals = [];
		this._readyPromise = new Promise(resolve => {
			this._readyResolve = resolve;
		});
		this._registerAppModals();
		this.modalsRegistered = false
		this.queuedOnloadModals = [];
	}

	setContainer(container, parentId) {
		if (!container) {
			// eslint-disable-next-line no-console
			console.warn('Container is required.');
			return;
		}

		if (this._formModalContainer === container) {
			// eslint-disable-next-line no-console
			console.warn('The container is already set.');
			return;
		}

		this._formModalContainer = container;
		this._parentId = parentId;
		this._readyPromise.then(() => {
			this._renderRegisteredModals();
			this.queuedOnloadModals.forEach(openModal => openModal());
			this.queuedOnloadModals = [];
			this.modalsRegistered = true;
		});
	}

	_renderRegisteredModals() {
		const registeredModals = this.getRegisteredModals();
		const renderComponents = new Set();
		const modals = registeredModals
			.map(modal => {
				const { component, type, namespace } = modal;
				const modalKey = getModalComponentKey(type, namespace);
				if (renderComponents.has(component)) return null;
				renderComponents.add(component);

				return React.createElement(
					component,
					{
						key: type,
						ref: comp => {
							if (comp) {
								this._modalComponents[modalKey] = comp;
							}
						},
						context: 'form',
						modalId: `${this._parentId}_${type}`
					},
					null
				);
			})
			.filter(modal => modal !== null);

		render(<div>{modals}</div>, this._formModalContainer);
	}

	_registerAppModals() {
		const registeredModals = SnAppModal.getRegisteredModals();
		registeredModals.forEach(({ type, fnMapper, namespace }) => {
			this._mapAPI(type, namespace, fnMapper);
		});
	}

	_enqueueModal(type, namespace, modalKey, publicFnName, componentFnName, args) {
		let wrappedModalPromiseResolve = null;
		let wrappedModalPromsiseReject = null;
		var queuedModalExecutedPromise = new Promise((resolve, reject) => {
			wrappedModalPromiseResolve = resolve;
			wrappedModalPromsiseReject = reject;
		});
		var openQueuedModal = () => {
			const queuedModal = this._modalComponents[modalKey];
			if (!queuedModal) {
				const msgNotRegistered = `SnFormModal.${publicFnName} can't be rendered. ${type} has not been registered`;
				// eslint-disable-next-line no-console
				console.warn(msgNotRegistered);
				wrappedModalPromsiseReject(msgNotRegistered)
			}
			const renderModalFunc = queuedModal[componentFnName].apply(queuedModal, namespace ? [type, ...args] : args)
			renderModalFunc.then((response) =>
				wrappedModalPromiseResolve(response)
			).catch((response) => {
				wrappedModalPromsiseReject(response);
			})
		};
		if (this.queuedOnloadModals.length >= MAX_MODAL_QUEUE_SIZE) {
			const msgQueueFull = `Modals continue to be called but the modal queue is now full. No more will be added.`;
			// eslint-disable-next-line no-console
			console.warn(msgQueueFull);
			return Promise.reject(msgQueueFull);
		} else {
			this.queuedOnloadModals.push(openQueuedModal);
			return queuedModalExecutedPromise;
		}
	}

	_mapAPI(type, namespace, fnMapper) {
		let modalObj = namespace ? this.customModals : this;
		if (namespace) {
			if (!this.customModals[namespace])
				this.customModals[namespace] = {};
			modalObj = this.customModals[namespace];
		}

		const modalKey = getModalComponentKey(type, namespace);
		_.forEach(fnMapper, (publicFnName, componentFnName) => {
			modalObj[publicFnName] = (...args) => {
				if (this.modalsRegistered) {
					const modal = this._modalComponents[modalKey];
					if (!modal && this.modalsRegistered) {
						const msg = `SnFormModal.${publicFnName} is not ready. ${type} is not rendered yet.`;
						// eslint-disable-next-line no-console
						console.warn(msg);
						return Promise.reject(msg);
					}
					else if (modal)
						return namespace
							? modal[componentFnName].apply(modal, [type, ...args])
							: modal[componentFnName].apply(modal, args);
				}
				else
					// If a modal is trying to open but the modal container is not registered,
					// store the modal call in a queue, to be opened after the modal registration is complete
					return this._enqueueModal(type, namespace, modalKey, publicFnName, componentFnName, args);
			};
		});
	}

	registerGlideModal(contentComponentTag, namespace, publicAPI) {
		if (!namespace) {
			console.warn(
				`Error: Namespace expected while registering glide modal for ${contentComponentTag}`
			);
			return;
		}
		this._registerModal(
			SnCustomModal,
			contentComponentTag,
			{ open: publicAPI },
			namespace
		);
	}

	getRegisteredModals() {
		const registeredModals = SnAppModal.getRegisteredModals();
		return [...registeredModals, ...this.registeredCustomModals];
	}

	_registerModal(component, type, fnMapper, namespace) {
		const registeredModals = this.getRegisteredModals();
		if (!component) {
			// eslint-disable-next-line no-console
			console.warn(
				'Error while registering modal. A component is required.'
			);
			return;
		}

		if (
			!fnMapper ||
			typeof fnMapper !== 'object' ||
			Object.keys(fnMapper).length == 0
		) {
			// eslint-disable-next-line no-console
			console.warn('Function mapper object is required.');
			return;
		}

		if (
			_.findIndex(
				registeredModals,
				reg => reg.type === type && reg.namespace === namespace
			) >= 0
		) {
			return;
		}

		const existFn = Object.values(fnMapper).filter(fn =>
			namespace
				? _.get(this.customModals, `${namespace}.${fn}`)
				: _.get(this, fn)
		);
		if (existFn.length > 0) {
			// eslint-disable-next-line no-console
			console.warn(
				`Public function name${
					existFn.length > 1 ? 's' : ''
				} '${existFn}' ${
					existFn.length > 1 ? 'are' : 'is'
				} already existed.`
			);
			return;
		}

		this.registeredCustomModals = [
			...this.registeredCustomModals,
			{
				component,
				type,
				fnMapper,
				namespace
			}
		];

		this._mapAPI(type, namespace, fnMapper);
	}

	initialize() {
		SnAppModal.onInitialized().then(() => {
			this._readyResolve();
		});
	}
}

FormModal.clone = original => {
	const formModal = new FormModal();
	if (original)
		formModal.setContainer(
			original._formModalContainer,
			original._parentId
		);
	return formModal;
};
