import React from 'react';
import PropTypes from 'prop-types';
import {
	BUTTON_CLICKED,
	ModalButtonStyle,
	ModalSize,
	ModalButtonAlignment,
	ModalButtonType,
	ModalButtonStyleVariant,
	SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN
} from '../../constants';
import { getScrollOffset, freezeBody, unfreezeBody } from '../utils/dom';
import react from '@servicenow/ui-renderer-react';
import { keyUtils } from '@devsnc/library-form';
import '@servicenow/now-modal';
import { ATFHelper } from '@devsnc/library-form';

const { createSlot } = react;
export const NOW_MODAL_BUTTON_CLICKED = 'NOW_MODAL#FOOTER_ACTION_CLICKED';
export const NOW_MODAL_OPENED = 'NOW_MODAL#OPENED_SET';

const convertSizeToNowModalSize = size => {
	switch (size) {
		case ModalSize.XL:
			return ModalSize.LG;
		case ModalSize.XS:
			return ModalSize.SM;
	}

	return size;
};

const convertButtonStyleToVariant = buttonStyle => {
	switch (buttonStyle) {
		case ModalButtonStyle.CONFIRM:
			return ModalButtonStyleVariant.PRIMARY;
		case ModalButtonStyle.DEFAULT:
			return ModalButtonStyleVariant.SECONDARY;
		case ModalButtonStyle.DESTRUCTIVE:
			return ModalButtonStyleVariant.PRIMARY_NEGATIVE;
	}

	return 'secondary';
};

const getNowModalButtonsJson = buttons => {
	if (!buttons || !buttons.length) return [];
	return buttons.map(({ title: label, buttonStyle, disabled = false, isConfirmButton = false }) => ({
		variant: convertButtonStyleToVariant(buttonStyle),
		label,
		disabled: disabled ? 'true' : false,
		payload: {
			title: label,
			buttonStyle,
			disabled,
			isConfirmButton
		}
	}));
};

const getFooterButtonActionMeta = payload => {
	const {
		footerAction: { payload: passThruPayload }
	} = payload;
	return {
		buttonType: ModalButtonType.FOOTER,
		...passThruPayload
	};
};

const handleEscKey = e => {
	if (keyUtils.onEscape(e)) e.stopPropagation();
};

class SnModal extends React.Component {
	constructor(props) {
		super(props);
		this.windowOffset = 0;
		this.modalRef = null;
		this.state = {
			modalData: null
		};
		this.onButtonClick = this.onButtonClick.bind(this);
		this.focusOnBody = this.focusOnBody.bind(this);
		this.onFocus = this.onFocus.bind(this);
		this.atfHelper = ATFHelper(window);
	}

	componentWillReceiveProps(nextProps) {
		if (
			nextProps.buttonClicked &&
			nextProps.buttonClicked != this.props.buttonClicked
		) {
			const { type, payload } = nextProps.buttonClicked;

			if (type === NOW_MODAL_OPENED) {
				const { value } = payload;
				if (value === false)
					this.onButtonClick({ buttonType: ModalButtonType.HEADER });
			} else if (type === NOW_MODAL_BUTTON_CLICKED) {
				this.onButtonClick({
					...getFooterButtonActionMeta(payload),
					data: this.state.modalData
				});
			}
		} else if (
			nextProps.modalData &&
			nextProps.modalData != this.props.modalData
		)
			this.setState({ modalData: nextProps.modalData });
	}

	onButtonClick(meta) {
		const {
			dispatch,
			properties: { onButtonClick, actionMetaData }
		} = this.props;

		const payload = {
			modalId: this.props.componentId,
			...meta,
			actionMetaData
		};

		setTimeout(() => {
			dispatch(BUTTON_CLICKED, payload);
		});

		if (onButtonClick) {
			onButtonClick(meta);
		}
	}

	setModalRef = el => {
		this.modalRef = el;
		if (this.modalRef) {
			this.modalRef.addEventListener('keydown', handleEscKey);
			this.atfHelper.initAtfModalRef(this.modalRef);
		} else this.atfHelper.clearAtfModalRef();
	};

	// Focus on the body only if there are no buttons, or the header is hidden
	// otherwise the footer will focus on the first button.
	focusOnBody() {
		const {
			buttons,
			shouldFocusOnOpen,
			headerHidden
		} = this.props.properties;

		if (!this.modalRef) {
			return;
		}

		if (
			shouldFocusOnOpen &&
			!(buttons && buttons.length > 0) &&
			!!headerHidden
		) {
			window.requestAnimationFrame(() => {
				this.modalRef.focus();
			});
		}
	}

	componentDidMount() {
		this.focusOnBody();
		if (this.props.properties.show)
			this.props.dispatch(SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN, true);
	}

	componentDidUpdate() {
		this.focusOnBody();
	}

	componentWillUnmount() {
		if (this.modalRef) {
			this.modalRef.removeEventListener('keydown', handleEscKey);
			this.atfHelper.clearAtfModalRef();
		}
		unfreezeBody(this.windowOffset);
	}

	renderContent() {
		const { children } = this.props.properties;
		return createSlot(children, 'content');
	}

	onFocus() {
		if (this.modalRef) {
			this.modalRef.focus();
		}
	}

	render() {
		const {
			componentId,
			properties: { modalTitle, buttons = [], show, noSpacing, size }
		} = this.props;

		if (!show) {
			this.atfHelper.clearAtfModalRef();
			unfreezeBody(this.windowOffset);;
		}

		this.windowOffset = getScrollOffset();
		freezeBody(this.windowOffset);
		const modalContent = this.renderContent();
		const modalButtons = getNowModalButtonsJson(buttons);
		const modalSize = convertSizeToNowModalSize(size);
		return (
			<now-modal
				opened={show}
				size={modalSize}
				header-label={modalTitle}
				footer-actions={modalButtons}
				component-name={componentId}
				component-id={`now_modal_${componentId}`}
				manage-opened
				ref={this.setModalRef}
				content-full-width={!!noSpacing}
			>
				{modalContent}
			</now-modal>
		);
	}
}

SnModal.propTypes = {
	show: PropTypes.bool,
	size: PropTypes.oneOf(Object.values(ModalSize)),
	modalTitle: PropTypes.string,
	headerHidden: PropTypes.bool,
	buttons: PropTypes.arrayOf(
		PropTypes.shape({
			title: PropTypes.string.isRequired,
			buttonStyle: PropTypes.oneOf(Object.values(ModalButtonStyle)),
			disabled: PropTypes.bool
		})
	),
	buttonAlignment: PropTypes.oneOf(Object.values(ModalButtonAlignment)),
	onButtonClick: PropTypes.func,
	actionMetaData: PropTypes.object,
	context: PropTypes.string,
	noSpacing: PropTypes.bool,
	shouldFocusOnOpen: PropTypes.bool
};

SnModal.defaultProps = {
	size: ModalSize.SM,
	buttonAlignment: ModalButtonAlignment.RIGHT,
	autofocus: true
};

export default SnModal;
