import React from 'react';
import react from '@servicenow/ui-renderer-react';
import SnMessageModal from '../messageModal/SnMessageModal';
import {
	ModalButtonAlignment,
	ModalButtonStyle,
	BUTTON_TYPE_ARRAY
} from '../../constants';
import { t } from 'sn-translate';
import _ from 'lodash';

const ALERT_MODAL_ID = 'sn-modal-alert';
const DEFAULT_CANCEL_TITLE = t('Cancel');
const DEFAULT_CONFIRM_TITLE = t('OK');
const DEFAULT_TITLE = t('Close');

class SnAlertModal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			show: false,
			modalTitle: '',
			message: ''
		};
		this.onButtonClick = this.onButtonClick.bind(this);
		this._promiseResolve = null;
		this._promiseReject = null;
	}

	onButtonClick(button) {
		setTimeout(() => {
			this.setState({ show: false });
		});

		if (button.isConfirmButton) {
			if (this._promiseResolve)
				this._promiseResolve(button);
		}
		else if (this._promiseReject)
				this._promiseReject(button);

		this._promiseResolve = null;
		this._promiseReject = null;
	}

	confirm({
		title,
		message,
		cancelTitle,
		confirmTitle,
		cancelType,
		confirmType
	}) {
		if (BUTTON_TYPE_ARRAY.indexOf(cancelType) === -1) {
			cancelType = ModalButtonStyle.DEFAULT;
			console.error(
				'Incorrect cancelType applied, please use one of the following options: ' +
					BUTTON_TYPE_ARRAY
			);
		}
		if (BUTTON_TYPE_ARRAY.indexOf(confirmType) === -1) {
			confirmType = ModalButtonStyle.CONFIRM;
			console.error(
				'Incorrect confirmType applied, please use one of the following options: ' +
					BUTTON_TYPE_ARRAY
			);
		}
		const buttons = [
			{
				title: confirmTitle || DEFAULT_CONFIRM_TITLE,
				buttonStyle: confirmType || ModalButtonStyle.CONFIRM,
				isConfirmButton: true
			},
			{
				title: cancelTitle || DEFAULT_CANCEL_TITLE,
				buttonStyle: cancelType || ModalButtonStyle.DEFAULT,
				isConfirmButton: false
			}
		];
		return this._alert(title, message, buttons);
	}

	confirmDestroy({
		title,
		message,
		cancelTitle,
		confirmTitle,
		cancelType,
		confirmType
	}) {
		if (BUTTON_TYPE_ARRAY.indexOf(cancelType) === -1) {
			cancelType = ModalButtonStyle.DEFAULT;
			console.error(
				'Incorrect cancelType applied, please use one of the following options: ' +
					BUTTON_TYPE_ARRAY
			);
		}
		if (BUTTON_TYPE_ARRAY.indexOf(confirmType) === -1) {
			confirmType = ModalButtonStyle.DESTRUCTIVE;
			console.error(
				'Incorrect confirmType applied, please use one of the following options: ' +
					BUTTON_TYPE_ARRAY
			);
		}
		const buttons = [
			{
				title: confirmTitle || DEFAULT_CONFIRM_TITLE,
				buttonStyle: confirmType || ModalButtonStyle.DESTRUCTIVE,
				isConfirmButton: true
			},
			{
				title: cancelTitle || DEFAULT_CANCEL_TITLE,
				buttonStyle: cancelType || ModalButtonStyle.DEFAULT,
				isConfirmButton: false
			}
		];
		return this._alert(title, message, buttons);
	}

	info({ title, message, buttonTitle, buttonType }) {
		if (BUTTON_TYPE_ARRAY.indexOf(buttonType) === -1) {
			buttonType = ModalButtonStyle.DEFAULT;
			console.error(
				'Incorrect buttonType applied, please use one of the following options: ' +
					BUTTON_TYPE_ARRAY
			);
		}
		const buttons = [
			{
				title: buttonTitle || DEFAULT_TITLE,
				buttonStyle: buttonType || ModalButtonStyle.DEFAULT,
				isConfirmButton: true
			}
		];
		return this._alert(title, message, buttons);
	}

	_alert(modalTitle, message, buttons) {
		this.setState({
			show: true,
			modalTitle,
			message,
			buttons
		});

		return new Promise((resolve, reject) => {
			this._promiseResolve = resolve;
			this._promiseReject = reject;
		});
	}

	render() {
		const { show } = this.state;
		const { modalTitle, ...props } = this.state;
		const { modalId = ALERT_MODAL_ID, ...passingProps } = _.omit(
			this.props,
			['children']
		);
		return (
			<SnMessageModal
				modalId={modalId}
				modalTitle={modalTitle}
				{...props}
				{...passingProps}
				show={show}
				onButtonClick={this.onButtonClick}
				buttonAlignment={ModalButtonAlignment.FULL}
			/>
		);
	}
}

export default SnAlertModal;
