import React from 'react';
import PropTypes from 'prop-types';
import {
	ModalButtonAlignment,
	ModalButtonStyle,
	ModalSize
} from '../../modal/constants';
import SnEmbeddedModal from '../../modal/components/embededModal/SnEmbeddedModal';
import { BasePropTypes, default as FieldSet } from './subcomponents/FieldSet';
import {
	FORM_FIELD_MODAL_CANCELED,
	FORM_FIELD_MODAL_CONFIRMED
} from '../constants';
import { FieldType } from '@devsnc/sn-glide-form-controls';
import { escapeSeismicSpecialSymbols } from '@devsnc/library-form';
import { ATFHelper } from '@devsnc/library-form';

const escapeValue = fields => {
	return fields.map(f => {
		return {
			...f,
			value: escapeSeismicSpecialSymbols(f.value),
			displayValue: escapeSeismicSpecialSymbols(f.displayValue),
			stagedValue: escapeSeismicSpecialSymbols(f.stagedValue)
		};
	});
};

class FieldSetModal extends React.Component {
	constructor(props) {
		super(props);
		const { fields = [], buttons = [] } = props.properties;
		this.setValue = this.setValue.bind(this);
		this.setStagedValue = this.setStagedValue.bind(this);
		this.state = {
			fields: escapeValue(fields)
		};
		const updatedButtons = this._disableButtons(buttons);
		this.state.buttons = updatedButtons;
		this.onButtonClick = this.onButtonClick.bind(this);
		this.atfHelper = ATFHelper(window);
		this.atfHelper.initSetFieldValue(this.state.fields, this.setValue);
	}

	componentWillReceiveProps(props) {
		let settingState = {};
		const { fields, buttons } = props.properties;
		if (fields !== this.props.properties.fields) {
			settingState.fields = escapeValue(fields);
		}

		if (buttons !== this.props.properties.buttons) {
			settingState.buttons = buttons;
		}

		if (Object.values(settingState).length > 0) {
			this.setState(settingState, this.disableButtonIfNecessary);
		}
	}

	onButtonClick(button) {
		const {
			properties: { onButtonClick },
			dispatch
		} = this.props;
		const data = { updatedFields: this.state.fields };
		switch (button.buttonStyle) {
			case ModalButtonStyle.CONFIRM:
			case ModalButtonStyle.DESTRUCTIVE:
				dispatch(FORM_FIELD_MODAL_CONFIRMED, {
					clickedButton: button,
					...data
				});
				if (onButtonClick) {
					onButtonClick(button, data);
				}
				break;
			default:
				dispatch(FORM_FIELD_MODAL_CANCELED, {
					clickedButton: button,
					timestamp: Date.now()
				});
				if (onButtonClick) {
					onButtonClick(button);
				}
		}
	}

	setValue(e, name, value, displayValue) {
		let changed = false;
		const fields = this.state.fields.map(field => {
			if (field.name === name) {
				if (
					field.value !== value ||
					field.displayValue !== displayValue
				)
					changed = true;

				switch (field.type) {
					case FieldType.GLIDE_LIST:
						return {
							...field,
							value,
							displayValue: displayValue.join(', '),
							display_value_list: displayValue
						};
					default:
						return {
							...field,
							value,
							displayValue
						};
				}
			}
			return field;
		});

		if (changed) {
			this.setState(
				{
					fields: escapeValue(fields)
				},
				this._disableButtonIfNecessary
			);
		}
	}

	setStagedValue(e, name, value) {
		let changed = false;
		const fields = this.state.fields.map(field => {
			if (field.name === name) {
				if (field.stagedValue !== value) changed = true;

				return {
					...field,
					stagedValue: escapeSeismicSpecialSymbols(value)
				};
			}
			return field;
		});

		if (changed) {
			this.setState(
				{
					fields
				},
				this._disableButtonIfNecessary
			);
		}
	}

	_disableButtons(buttons) {
		const { fields } = this.state;
		const disableButton =
			fields.filter(
				f =>
					f.mandatory &&
					((f.stagedValue !== undefined && !f.stagedValue) ||
						(f.stagedValue === undefined && !f.value))
			).length > 0;
		let stateChanged = false;
		const newButtons = buttons.map((button, buttonIndex) => {
			if (buttonIndex === 0) {
				stateChanged =
					stateChanged || !!button.disabled !== disableButton;

				return {
					...button,
					disabled: disableButton
				};
			}
			return { ...button };
		});
		return stateChanged ? newButtons : buttons;
	}

	_disableButtonIfNecessary() {
		const { buttons } = this.state;
		const newButtons = this._disableButtons(buttons);
		if (newButtons !== buttons) {
			this.setState({
				buttons: newButtons
			});
		}
	}

	_isFieldToFocus() {
		const { fields } = this.state;
		return fields.filter(f => !f.readOnly).length > 0;
	}

	render() {
		const { fields, buttons } = this.state;
		let {
			componentId,
			properties: { instruction, shouldFocusOnOpen, show, ...modalProps }
		} = this.props;
		if (!show) return null;

		delete modalProps.onButtonClick;
		delete modalProps.buttons;
		const modalId = `${componentId}-base-modal`;
		//pass shouldFocusOnOpen to base modal component to focus header or footer's buttons if there's no focusable fields
		const shouldFocusOnOpenAttr =
			shouldFocusOnOpen && !this._isFieldToFocus()
				? { shouldFocusOnOpen: true }
				: {};
		return (
			<SnEmbeddedModal
				modalId={modalId}
				buttons={buttons}
				{...modalProps}
				show={show}
				{...shouldFocusOnOpenAttr}
				onButtonClick={this.onButtonClick}
				ariaDescribedby="modal-field-set-instruction-span"
			>
				<FieldSet
					componentId={modalId}
					fields={fields}
					onValueChange={this.setValue}
					onStagedValueChange={this.setStagedValue}
					instruction={instruction}
				/>
			</SnEmbeddedModal>
		);
	}
}

FieldSetModal.proptypes = {
	...BasePropTypes,
	show: PropTypes.bool,
	size: PropTypes.oneOf(Object.values(ModalSize)),
	modalTitle: PropTypes.string,
	headerHidden: PropTypes.bool,
	buttons: PropTypes.arrayOf(
		PropTypes.shape({
			title: PropTypes.string.isRequired,
			buttonStyle: PropTypes.oneOf(Object.values(ModalButtonStyle)),
			disabled: PropTypes.bool
		})
	),
	buttonAlignment: PropTypes.oneOf(Object.values(ModalButtonAlignment)),
	onButtonClick: PropTypes.func
};

export default FieldSetModal;
